body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
nav.navbar, .navbar-default {
	background-color: black !important;
	height: 150px;
	z-index: 2;
}

.navbar-header{
	float:none;
	height: 100px;
}

.navbar-nav{
	float: none;
}

.collapse{
	text-align: center;
	float: none;
}

.navbar-collapse{
	text-align: center;
	background-color: black;
	width: 100%;
}

.navbar-default{
	background-image: none;
	border: none;
	border-radius: 0;
}

.navbar-default .navbar-nav{
	float: none;
	background-color: black;
	display: inline-block;
}

.navbar-default .navbar-nav > li > a{
	color: #404040;
	transition: border-bottom-left-radius 0.5s, border-top-right-radius 0.5s;
	background-color: #C0C0C0;
	margin-top: 0px;
	margin-bottom: 0px;
	margin-right: 1px;
	margin-left: 1px;
	padding: 5px;
	font-style: normal;
	-webkit-font-feature-settings: "c2sc";
	        font-feature-settings: "c2sc";
	font-variant: small-caps;
	font-weight: 800;
	font-size: 15px;	
	border-radius: 25px;
	width: 150px;
	text-align: center;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.32);
	outline: 0;
}

.navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover{
	border-bottom-left-radius: 2px;
	border-top-right-radius: 2px;
	background-color: #C0C0C0;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.32);
}

.navbar-default .navbar-toggle{
	border-color: #C0C0C0;
}

.navbar-toggle{
	margin: 20px;
}

.navbar-default .navbar-toggle .icon-bar{
	background-color: #C0C0C0;
}

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover{
	background-color: #D3D3D3;
}

.navbar-default .navbar-toggle:active, .navbar-default .navbar-toggle:active{
	color: #C0C0C0;
	background-color: #D3D3D3;
}

.navbar-default .navbar-brand-style {
	color: #C0C0C0;
	font-family: Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif; 
	font-size: 35px;
	font-style: normal;
	-webkit-font-feature-settings: "c2sc";
	        font-feature-settings: "c2sc";
	font-variant: small-caps;
	font-weight: 700;
	margin-top: 15px;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.32);
	border-color: #C0C0C0;
	text-align: center;
}

/* positional information to align photo with image */
.title-bar {
	border: 1px solid black;
	text-align: center;
	display: inline;
	overflow: hidden; /* add this to contain floated children */
}

.navbar-brand {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.navbar-brand>img {
  padding: 7px 14px;
}

container-fluid > .navbar-collapse, .container-fluid > .navbar-header, .container > .navbar-collapse, .container > .navbar-header{
	margin-right: 0px;
	margin-left: 0px;
}


/* adjustments made to the navbar orientation upon change of screen size */
@media screen and (max-width: 767px) {
	.navbar-content:before {
		content: "David H Smith IV";
	}

	.navbar-brand-style:before{
		font-size: 30px;
		width: 50%;
		float: left;
	} 

	nav.navbar, .navbar-default {
		background-color: black !important;
		height: 100px;
	}

}
  
@media screen and (min-width: 767px) {
	
	.navbar-content:before {
		content: "David H Smith IV";
		font-size: 50px;
	}

	.navbar-brand-style:before{
		width: 100%;
		display: inline-block;
		padding: 5px;
	}
}
